import React, { type ReactNode } from "react";
import { autoInput } from "../../AutoInput.js";
import type { ShadcnElements } from "../elements.js";

// lazy load the component to reduce the bundle size and to avoid errors importing .css files in SSR bundles
const ShadcnAutoDateTimeInputComponent = React.lazy(() => import("./ShadcnAutoDateTimeInputComponent.js"));

export interface DatePickerProps {
  onChange: (date: Date) => void;
  selected: Date;
  onSelect: (date: Date) => void;
  initialFocus: boolean;
  mode: "single" | "range";
  weekStartsOn: number;
  dayAccessibilityLabelPrefix: string;
  onMonthChange: (month: number, year: number) => void;
  allowRange: boolean;
  multiMonth: boolean;
  disableDatesBefore: Date;
  disableDatesAfter: Date;
  disableSpecificDates: Date[];
}

export const makeShadcnAutoDateTimePicker = ({
  Button,
  Calendar,
  Label,
  Popover,
  PopoverTrigger,
  PopoverContent,
}: Pick<ShadcnElements, "Button" | "Calendar" | "Label" | "Popover" | "PopoverTrigger" | "PopoverContent">) => {
  function ShadcnAutoDateTimePicker(props: {
    field: string;
    id?: string;
    value?: Date;
    onChange?: (value?: Date) => void;
    error?: string;
    includeTime?: boolean;
    hideTimePopover?: boolean;
    label?: ReactNode;
    datePickerProps?: Partial<DatePickerProps>;
    timePickerProps?: { label?: ReactNode; placeholder?: string };
  }) {
    return (
      <ShadcnAutoDateTimeInputComponent {...props} shadcnElements={{ Button, Calendar, Label, Popover, PopoverTrigger, PopoverContent }} />
    );
  }

  ShadcnAutoDateTimePicker.displayName = "ShadcnAutoDateTimePicker";

  return autoInput(ShadcnAutoDateTimePicker);
};
